@charset "utf-8";
@font-face {
    font-family: 'Ownglyph_StudyHard-Rg';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2411-3@1.0/Ownglyph_StudyHard-Rg.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Ownglyph_ttaerom-Rg';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2408-4@1.0/Ownglyph_ttaerom-Rg.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    color: inherit;
    display: block;
}
body{
    color: #333;
    font-family: 'Ownglyph_ttaerom-Rg';
}
.header{
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
    text-align: center;
    background-color: rgb(255, 210, 229);
    margin-top: 50px;
    padding-top: 20px;
    border-radius: 20px 20px 0 0;
}
.header .inner h1{
    margin-bottom: 10px;
    color: #ee3f85d8;
    font-size: 50px;
}
.header .inner span{
    font-size: 25px;
}
.check{
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
    background-color: rgb(255, 210, 229);
    padding: 50px 30px;
    border-radius: 0 0 20px 20px;
}
.check form{
    position: relative;
}
.check form input[type='text']{
    width: 100%;
    height: 50px;
    border: 1px solid #999;
    border-radius: 20px;
    padding-left: 10px;
}
.check form button{
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    border: none;
    background-color:  transparent;
}

.check_list ul{
    margin-top: 50px;
}
.check_list ul li{
    background-color: white;
    height: 80px;
    margin-top: 15px;
    line-height: 80px;
    padding-left: 30px;
    border-radius: 20px;
    position: relative;
    font-size: 25px;
}
.check_list ul li:hover{
    background-color: #ee3f85b9;
    color: #fff;
}
.check_list button{
    border: none;
    background-color: transparent;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
}
.check_list button img{
}

